import { useState } from 'react'

const Modal = ({projects , setPledgeAmount ,onShow , closeModal}) => {
    const [onConfirm , setConfirm] = useState(false)

    const ConfirmModal = ({onShow}) =>{
        return(
            <div className={`${onConfirm ? 'modal-show' : 'modal-hide'}  modal-bg modal-center`}>
                <div className="modal-confirm modal-container">
                    <div className="modal-header">
                        <img src="/images/icon-check.svg" alt="check" />
                    </div>
                    <div className="modal-content">
                        <h2>Thanks for your support!</h2>
                        <p>Your pledge brings us one step closer to sharing
                             Mastercraft Bamboo Monitor Riser worldwide. You will get
                                an email once our campaign is completed.</p>
                    </div>
                    <div className="modal-footer">
                        <button onClick={() => setConfirm(false)} className="btn-green">Got it!</button>
                    </div>
                </div>
            </div>
        )

    }

    const ProjectModal =() =>{
        return(
            <div className={`${onShow ? 'modal-show' : 'modal-hide'}  modal-bg modal-center`}>
            <div className="modal-container">
                <div className="modal-header">
                    <h2>Back this project</h2>
                    <p>Want to support us in bringing Mastercraft Bamboo Monitor Riser out in the world?</p>
                    <span><img onClick={closeModal} src="./images/icon-close-modal.svg" alt="close" /></span>
                </div>
                <div className="modal-content">
                    {projects.map((project) => (
                        <Project key={project.id} items={project} />
                    ))}
                </div>
            </div>
            </div>
        )
    }

    const Project = ({items}) =>{
        return(
        <div>
        {items.quantity > 1 && <input type="radio" name="select"  className="project-checkbox" /> }
        <div className={`${items.quantity < 1 && 'modal-fade'} modal-project`}>
            <div className="modal-project--header">
           {items.quantity > 1  ? <h4 className="--mainText" onClick={(e) => onChecked(e.target.parentElement)}><span></span>{items.name}</h4>
                                : <h4>{items.name}</h4>
            }
            <span>Pledge ${items.price} or more</span>
                <div>
                    <h4>{items.quantity}</h4>
                    <small>left</small>
                </div>
            </div>
            <div className="modal-project--text">
                <p>{items.description}</p>
            </div>
            <div className="modal-quantity-sm">
                <h2>{items.quantity}</h2>
                <small>left</small>
            </div>
            <div className="modal-project--footer">
            <input type="text" id="pledge-amount"  placeholder="Enter your pledge"/>
            <div>
                <button onClick={() => sendAmount(items.price)} className="btn-white"><span>$</span>{items.price}</button>
                <button onClick={enterAmount}  className="btn-green">Continue</button>
            </div>
            </div>
        </div>
        </div>
        )
    }

    const onChecked = (e) =>{
        const checkbox = e.parentElement.previousSibling
        if(!checkbox) {
            return;
        }
        checkbox.checked = true

    }

    const enterAmount = () =>{
        const amount = document.querySelector('#pledge-amount').value; 
        sendAmount(amount)
    }

    const sendAmount = (amount) =>{
        setPledgeAmount(amount)
        setConfirm(!onConfirm)
        closeModal()
    }
    return (
        onConfirm ? <ConfirmModal /> :  <ProjectModal />

       
    )
}

export default Modal
