const Project = ({projects}) => {
    const Items = ({items}) =>{
        return(
        <div className={`${items.quantity < 1 && 'empty'} project`}>
            <div className="project-header">
                <h4>{items.name}</h4>
                <span>Pledge ${items.price} or more</span>
            </div>

            <p className="project-text">{items.description}</p>
            
            <div className="project-footer">
                <div>
                <h1>{items.quantity}</h1>
                <small>left</small>
                </div>
                {items.quantity < 1 ? <button className="btn-green" disabled>Out of Stock</button> 
                                    : <button className="btn-green"> Select Reward</button> }
                
            </div>
        </div>
        )
    }
    return (
        projects.map((project) => (
            <Items key={project.id} items={project}/>
        ))
       

    )
}

export default Project
